.form-container{
  box-shadow: 
  12px 12px 16px 0 rgba(0, 0, 0, 0.25),
  -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  width: 400px;
  margin-top: 16px;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: whitesmoke;
  border: 1px solid transparent;
  border-radius: 16px;
}

.form-container input{
  width: 360px;
  height: 30px;
  background-color: white;
  color: black;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 1px solid transparent;
}


.form-container button{
    padding: 10px;
    width: 200px;
    margin-left: 100px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
}

.form-container h3{
  text-align: center;
  margin-bottom: 30px;
}

.form-container button:hover::after{
  width: 100%;
}