.header{
    width: 96vw;
    align-items: center;
    display: flex;
    justify-content: space-between;
    height: 60px;
    background-color: rgb(36, 82, 190);
    color: white;
    margin-bottom: 30px;
}

.nav-header{
    margin: 10px;
}

button{
    border-radius: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    margin-right: 30px;
}