.transaction-list-container {
  box-shadow: 
  12px 12px 16px 0 rgba(0, 0, 0, 0.25),
  -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  width: 300px;
  margin: 0 auto;
  border: 1px solid #ccc;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: whitesmoke;
  border: 1px solid transparent;
  border-radius: 16px;
}

.transaction-list-container h2 {
  text-align: center;
  margin: 10px;
  /* color: white; */
}



 .form-container button {
  padding: 5px ;
  font-size: 16px;
  color: white;
  border: none;
  cursor: pointer;
}



.transaction-item {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white;
  border-radius:10px ;
  box-shadow: 
  12px 12px 16px 0 rgba(0, 0, 0, 0.25);
}

.filter-container{
  display: flex;
  
}



.filter-container button {
  border: none;
  border-radius: 10px;
  background-color: #fff;
  color: #000;
}

.filter-container button.active {
  background-color: #007bff;
  color: #fff;
}


.button-gp{
  display: grid;
  grid-template-columns: auto auto;
  gap:20px;
}

.button-gp img{
  width: 30px;
  background-color: white;
  border-radius: 20px;
}

.button-gp button{
  background-color: white;
  border-radius: 20px;
}

.icon{
  width: 20px;
  height: 20px;
  margin-bottom: 12px;
}

.item{
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
}
.outer-container{
  display: grid;
  grid-template-columns: auto auto;
  gap: 40px;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}


.form-container{
  box-shadow: 
  12px 12px 16px 0 rgba(0, 0, 0, 0.25),
  -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  width: 300px;
  /* margin: 0 auto; */
  border: 1px solid #ccc;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: whitesmoke;
  border: 1px solid transparent;
  border-radius: 16px;
}

.form-container input{
  width: 260px;
  background-color: white;
  color: black;
  border-radius: 10px;
  border: 1px solid transparent;
}

.form-container select{
  width: 266px;
  background-color: white;
  padding: 10px;
  color: black;
  border-radius: 10px;
  border: 1px solid transparent;
}

.form-container button{
    padding: 10px;
    width: 200px;
    margin-left: 50px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
}

.form-container button:hover::after{
    width: 100%;
}

.form-container h3{
  text-align: center;
  margin-bottom: 30px;
}

.delete-icon{
  color: red;
  margin: 0;
  width: 40px;
  height: 40px;
}

@media (max-width: 730px) {
  
  .outer-container {
    display: flex;
    flex-direction: column; 
  }
}


