.transaction-summary {
    padding: 10px;
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 30px;
    align-items: center;
    justify-content: center;
  }

  h1{
    align-items: center;
    justify-content: center;
  }
  
  .summary-item {
    box-shadow: 
    12px 12px 16px 0 rgba(0, 0, 0, 0.25),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
    background-color: whitesmoke;
    border: 1px solid #007bff;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: auto;
    padding: 30px;
    margin-bottom: 10px;
    border-radius: 20px;
  }
  
  .summary-item p {
    margin: 0;
  }


  .income{
    color: green;
    font-weight: 900;
    font-size: large;
  }

  .expense{
    color: red;
    font-weight: 900;
    font-size: large;
  }

  .saving{
    color: rgb(208, 32, 227);
    font-weight: 900;
    font-size: large;
  }
  
  .summary-item p:first-child {
    font-weight: bold;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  
  
  